import React, { Component } from 'react';
import './App.css';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import $ from "jquery";
import Title from './Title';
import Classic from './Classic';
import Camarilla from './Camarilla';
import Fib from './Fib';
import Demark from './Demark';
import subscriptionkey from './config/subscriptionkey.js';

class App extends Component {
  constructor (props) {
    super(props);

    var defaultValue;

    if (localStorage.getItem('symbol')){
      defaultValue = localStorage.getItem('symbol');
    } else{
      defaultValue ='';
    }
    var holdings= []
    if (localStorage.getItem('holdings')){
      holdings = JSON.parse(localStorage.getItem('holdings'));
      holdings = holdings.sort(function(a, b) {return parseFloat(b.price)-parseFloat(a.price)})
    } 

    if((defaultValue === undefined) || (defaultValue === 'undefined') ) {
      defaultValue = ''
    };

    var total=0;
    holdings.forEach((a)=>total+=parseFloat(a.value))
    var shares=0; 
    holdings.forEach((a)=>shares+=parseFloat(a.quantity))
    var adjusted = (parseFloat(total)/parseFloat(shares)).toFixed(2)
    var selling = []


    this.state = {
      symbol: defaultValue, 
      stock:'',
      open: 0,
      high: 0,
      low: 0,
      date: "",
      close:0,
      classicPP: 0.00,
      range:0,
      x:0,
      message:'',
      change:"",
      changeColor: '',
      quantity:0,
      price:0,
      holdings: holdings,
      total:total,
      shares:shares,
      adjusted:adjusted,
      compareMessage:"can't sell yet",
      selling:selling,
      shares2:0,
      value2:0,
      high1:0,
      low1:0,
      close1:0,
      open1:0,
      date1:'',
      classicPP1:0.00,
      range1:0,
      x1:0,
      high2:0,
      low2:0,
      close2:0,
      open2:0,
      date2:'',
      classicPP2:0.00,
      range2:0,
      x2:0
  };


this.sellingUpdate = (x) => {
  var r = (x) => (Math.round(parseFloat(x)*100) *0.01).toFixed(2);
  var p = (x) => parseFloat(x);
  if(x ==="current"){
 var sellingPrice =     parseFloat(component.state.close)
  } else if (x === "r3"){
    var sellingPrice =      r(p(this.state.classicPP) + 2*(p(this.state.high) - p(this.state.low)))
  } else if (x === "r2"){
    var sellingPrice =    r(p(this.state.classicPP) +p(this.state.high) - p(this.state.low))
  } else if (x === "r1"){
    var sellingPrice =     r(2*p(this.state.classicPP) - p(this.state.low))
  } else if (x === "pp"){
    var sellingPrice =     parseFloat(component.state.classicPP)
  } else if (x === "s1"){
    var sellingPrice =      r(2*p(this.state.classicPP) - p(this.state.high)) 
  } else if (x === "s2"){
    var sellingPrice =     r(p(this.state.classicPP) - p(this.state.high) + p(this.state.low)) 
  } else if (x === "s3"){
    var sellingPrice =   r(p(this.state.classicPP) - 2*(p(this.state.high) - p(this.state.low)))
  }

if(sellingPrice>parseFloat(component.state.holdings[component.state.holdings.length-1].price) ){
  component.setState({compareMessage:'ready to sell'})
  var surplus =0;
  var selling =[];
  var shares2 =0;
  var value2 =0;
  for(var i = component.state.holdings.length; i>0; i--){
    if((sellingPrice - component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity >0) {
      surplus += parseInt((sellingPrice - component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity)
      shares2+=parseInt(component.state.holdings[i-1].quantity);
      value2 += parseFloat(component.state.holdings[i-1].quantity*component.state.holdings[i-1].price)
      var newObject2 = {price: component.state.holdings[i-1].price, quantity: component.state.holdings[i-1].quantity}
      selling.push(newObject2)
    } else if(surplus>(component.state.holdings[i-1].price-sellingPrice)){
      var newquantity =0
     for (let j = 0; j < component.state.holdings[i-1].quantity; j++) {
       if(surplus>(component.state.holdings[i-1].price-sellingPrice)){
     surplus -= (component.state.holdings[i-1].price-sellingPrice)
       newquantity++
       shares2++;
       value2 += parseFloat(component.state.holdings[i-1].price)
     }
     }
     var newObject3 = {price: component.state.holdings[i-1].price, quantity: newquantity}
     selling.push(newObject3)
    }
  }
  component.setState({selling,shares2,value2})
} else{
  component.setState({compareMessage:"can't sell yet", selling:[],shares2:0, value2:0})
}
}
 
  
this.enterSymbol = (event) => {
    this.setState({symbol: event.target.value.toUpperCase()});
}

this.enterQuantity = (event) => {
  this.setState({quantity: event.target.value});
}
this.enterPrice = (event) => {
  this.setState({price:parseFloat(event.target.value).toFixed(2)});
}
this.quantityEnter = () =>{
  var newObject = {price:this.state.price, quantity:this.state.quantity, value:this.state.price*this.state.quantity}
  holdings.push(newObject)
  this.setState({holdings})
  localStorage.setItem('holdings', JSON.stringify(holdings));
  console.log(holdings)
  var total=0;
  holdings.forEach((a)=>total+=parseFloat(a.value))
  var shares=0; 
  holdings.forEach((a)=>shares+=parseFloat(a.quantity))
  var adjusted = (parseFloat(total)/parseFloat(shares)).toFixed(2)
  holdings = holdings.sort(function(a, b) {return parseFloat(b.price)-parseFloat(a.price)})
  this.setState({total,shares,adjusted,holdings})
  var close = parseFloat(component.state.close)
if(close>parseFloat(component.state.holdings[component.state.holdings.length-1].price) ){
  component.setState({compareMessage:'ready to sell'})
  var surplus =0;
  var selling =[];
  var shares2 =0;
  var value2 =0;
  for(var i = component.state.holdings.length; i>0; i--){
    if((close - component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity >0) {
      surplus += parseInt(close - component.state.holdings[i-1].price)*parseInt(component.state.holdings[i-1].quantity)
      shares2+=parseInt(component.state.holdings[i-1].quantity);
      value2 += parseFloat(component.state.holdings[i-1].quantity*component.state.holdings[i-1].price)
      var newObject2 = {price: component.state.holdings[i-1].price, quantity: component.state.holdings[i-1].quantity}
      selling.push(newObject2)
    } else if(surplus>(component.state.holdings[i-1].price-close)){
      var newquantity =0
     for (let j = 0; j < component.state.holdings[i-1].quantity; j++) {
       if(surplus>(component.state.holdings[i-1].price-close)){
     surplus -= (component.state.holdings[i-1].price-close)
       newquantity++
       shares2++;
       value2 += parseFloat(component.state.holdings[i-1].price)
     }
     }
     var newObject3 = {price: component.state.holdings[i-1].price, quantity: newquantity}
     selling.push(newObject3)
    }
  }
  component.setState({selling,shares2,value2})
} else{
  component.setState({compareMessage:"can't sell yet", selling:[],shares2:0, value2:0})
}
}

this.delete = (index) =>{
holdings.splice(index,1);
this.setState({holdings})
var total=0;
  holdings.forEach((a)=>total+=parseFloat(a.value))
  var shares=0; 
  holdings.forEach((a)=>shares+=parseFloat(a.quantity))
  var adjusted = (parseFloat(total)/parseFloat(shares)).toFixed(2)
  holdings = holdings.sort(function(a, b) {return parseFloat(b.price)-parseFloat(a.price)})
  this.setState({total,shares,adjusted,holdings})
localStorage.setItem('holdings', JSON.stringify(holdings));
var close = parseFloat(component.state.close)
if(close>parseFloat(component.state.holdings[component.state.holdings.length-1].price) ){
  component.setState({compareMessage:'ready to sell'})
  var surplus =0;
  var selling =[];
  var shares2 =0;
  var value2 =0;
  for(var i = component.state.holdings.length; i>0; i--){
    if((close - component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity >0) {
      surplus += parseInt(close - component.state.holdings[i-1].price)*parseInt(component.state.holdings[i-1].quantity)
      shares2+=parseInt(component.state.holdings[i-1].quantity);
      value2 += parseFloat(component.state.holdings[i-1].quantity*component.state.holdings[i-1].price)
      var newObject2 = {price: component.state.holdings[i-1].price, quantity: component.state.holdings[i-1].quantity}
      selling.push(newObject2)
    } else if(surplus>(component.state.holdings[i-1].price-close)){
      var newquantity =0
     for (let j = 0; j < component.state.holdings[i-1].quantity; j++) {
       if(surplus>(component.state.holdings[i-1].price-close)){
     surplus -= (component.state.holdings[i-1].price-close)
       newquantity++
       shares2++;
       value2 += parseFloat(component.state.holdings[i-1].price)
     }
     }
     var newObject3 = {price: component.state.holdings[i-1].price, quantity: newquantity}
     selling.push(newObject3)
    }
  }
  component.setState({selling,shares2,value2})
} else{
  component.setState({compareMessage:"can't sell yet", selling:[],shares2:0, value2:0})
}

}

this.store =(set) =>{
  if(set == 1){
  let close1  = this.state.close;
  let open1  = this.state.open;
 let  high1  = this.state.high;  
 let  low1  = this.state.low;
 let  date1  = this.state.date;
 let  classicPP1  = this.state.classicPP;
 let  range1  = this.state.range;
 let  x1  = this.state.x;
this.setState({close1, open1, high1, low1, date1, classicPP1, range1,x1})
localStorage.setItem('store1', JSON.stringify({close1, open1, high1, low1, date1, classicPP1, range1,x1}));
  } else if( set ==2){
  let   close2  = this.state.close;
let    open2  = this.state.open;
let    high2  = this.state.high; 
let    low2  = this.state.low;
let    date2  = this.state.date;
let    classicPP2  = this.state.classicPP;
let    range2  = this.state.range;
let    x2  = this.state.x;
  this.setState({close2, open2, high2, low2, date2, classicPP2, range2,x2})
  localStorage.setItem('store2', JSON.stringify({close2, open2, high2, low2, date2, classicPP2, range2,x2}));
  }
}

this.recall =(set) =>{
  if(set == 1){
  let close  = this.state.close1;
  let open  = this.state.open1;
 let  high  = this.state.high1;  
 let  low  = this.state.low1;
 let  date  = this.state.date1;
 let  classicPP  = this.state.classicPP1;
 let  range  = this.state.range1;
 let  x  = this.state.x1;
this.setState({close, open, high, low, date, classicPP, range,x})
  } else if( set ==2){
  let   close  = this.state.close2;
let    open  = this.state.open2;
let    high  = this.state.high2; 
let    low  = this.state.low2;
let    date  = this.state.date2;
let    classicPP  = this.state.classicPP2;
let    range  = this.state.range2;
let    x  = this.state.x2;
  this.setState({close, open, high, low, date, classicPP, range,x})
  }
}

this.keyPress = (event) => {
  if(event.keyCode === 13){
    this.setState({symbol: event.target.value.toUpperCase()});
    this.lookup();
 }
}


var component = this;

this.lookup = () => {

  component.setState({message:'Crunching numbers...', change:''});
  $.ajax({
    url: 'https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol='+component.state.symbol+ '&apikey=' + subscriptionkey, 
    // component.state.symbol.toUpperCase()
    crossDomain: true,
    dataType: "json",
    success: function(data, textStatus, jqXHR) {
      if(data.Information !== undefined){
        component.setState({message:'Server error or busy. Please try again in a minute'});
      } else if (typeof data['Global Quote']["03. high"] === 'undefined') {
        component.setState({message:'Could not find symbol. Please check spelling and try again'});
      } else{
        // var info = data['Global Quote'];
        var stock =  data['Global Quote']["01. symbol"];
        var date =  'Based on last trading session: ' + data['Global Quote']["07. latest trading day"];
        var high = parseFloat(data['Global Quote']["03. high"]).toFixed(2);
        var low =  parseFloat(data['Global Quote']["04. low"]).toFixed(2);
        var open = parseFloat(data['Global Quote']["02. open"]).toFixed(2);
        var close = parseFloat(data['Global Quote']["05. price"]).toFixed(2);
        var change = ' ( $ ' + parseFloat(data['Global Quote']["09. change"]).toFixed(2) + " , "+ parseFloat(data['Global Quote']['10. change percent']).toFixed(2) + '% )';
        var classicPP = ((parseFloat(high)+parseFloat(low)+parseFloat(close))/3).toFixed(2);
        var range = (parseFloat(high)-parseFloat(low)).toFixed(2);
        var x;
        var changeColor = '';
        if( data['Global Quote']["09. change"] >= 0){changeColor = "green"};
        if( data['Global Quote']["09. change"] < 0){changeColor = "red"};
        if( open > high) {high = open}; //sometimes open price is higher than high?? Strange info from AlphaVantage API. This will correct the issue
        if( close > high) {high = close};//sometimes close price is higher than high?? Strange info from AlphaVantage API. This will correct the issue
        if( open < low) {low = open};//sometimes open price is lower than low?? Strange info from AlphaVantage API. This will correct the issue
        if( close < low) {low = close};//sometimes close price is lower than low?? Strange info from AlphaVantage API. This will correct the issue
        if(close === 'NaN'){
          component.setState({message:'Server error or busy. Please try again in a minute'});
        };
        // adding comparison ------------------------
        if((component.state.holdings.length>2)){
         if((close>parseFloat(component.state.holdings[component.state.holdings.length-1].price) ) ){
           component.setState({compareMessage:'ready to sell'})
           var surplus =0;
           var selling =[];
           var shares2 =0;
           var value2 =0;
           for(var i = component.state.holdings.length; i>0; i--){
             if((close - component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity >0) {
               surplus += parseInt((close) - parseInt(component.state.holdings[i-1].price)*component.state.holdings[i-1].quantity)
               shares2+=parseInt(component.state.holdings[i-1].quantity);
               value2 += parseFloat(component.state.holdings[i-1].quantity*component.state.holdings[i-1].price)
               var newObject2 = {price: component.state.holdings[i-1].price, quantity: component.state.holdings[i-1].quantity}
               selling.push(newObject2)
             } else if(surplus>(component.state.holdings[i-1].price-close)){
               var newquantity =0
              for (let j = 0; j < component.state.holdings[i-1].quantity; j++) {
                if(surplus>(component.state.holdings[i-1].price-close)){
              surplus -= (component.state.holdings[i-1].price-close)
                newquantity++
                shares2++;
                value2 += parseFloat(component.state.holdings[i-1].price)
              }
              }
              var newObject3 = {price: component.state.holdings[i-1].price, quantity: newquantity}
              selling.push(newObject3)
             }
           }
           component.setState({selling,shares2,value2})
          } else{
            component.setState({compareMessage:"can't sell yet", selling:[],shares2:0, value2:0})
          }}
        // adding comparison ------------------------

        if(parseFloat(close) > parseFloat(open)){
          x=2*parseFloat(high )+ parseFloat(low) + parseFloat(close);
        } else if(parseFloat(close) < parseFloat(open)){
          x=parseFloat(high )+ 2*parseFloat(low) + parseFloat(close);
        } else if(parseFloat(close) === parseFloat(open)){
          x=parseFloat(high )+ parseFloat(low) + 2*parseFloat(close);
        }
        component.setState( {high, low, stock,close, open,date,classicPP, range,x, change, changeColor, message:date});
        if( stock === undefined){ stock = ''};
        localStorage.setItem('symbol', stock);
      }},
    error: function (jqXHR, textStatus, error) {
        console.log("Post error: " + error);
        component.setState({message:'Server error or busy. Please try again in a minute'});
    }
});
}
}

componentDidMount(){
  if(localStorage.getItem('symbol')){
  this.lookup();
}

if (localStorage.getItem('store1')){
  var store1 = JSON.parse(localStorage.getItem('store1'));
  let close1  = store1.close1;
  let open1  = store1.open1;
 let  high1  = store1.high1;  
 let  low1  = store1.low1;
 let  date1  = store1.date1;
 let  classicPP1  = store1.classicPP1;
 let  range1  = store1.range1;
 let  x1  = store1.x1;
this.setState({close1, open1, high1, low1, date1, classicPP1, range1,x1})
} 
if (localStorage.getItem('store2')){
  var store2 = JSON.parse(localStorage.getItem('store2'));
  let close2  = store2.close2;
  let open2  = store2.open2;
 let  high2  = store2.high2;  
 let  low2  = store2.low2;
 let  date2  = store2.date2;
 let  classicPP2  = store2.classicPP2;
 let  range2  = store2.range2;
 let  x2  = store2.x2;
this.setState({close2, open2, high2, low2, date2, classicPP2, range2,x2})
} 

}



  render() {
    return (
      <div className="App">
      <div className="container">
        <div className='content'>
          <h1> Pivot Points </h1>
          <TextField id="symbol" type="text" defaultValue={localStorage.getItem('symbol') || ""} onKeyDown={this.keyPress} label="Stock Symbol" onChange={this.enterSymbol}  />
          <br/>
          <Button size="small" style={{Height: '1vmin'}} id='button' variant="contained" color="primary" onClick={this.lookup}> Look up </Button>
         <br/>
         <div style={{marginBottom:'1vmin'}}> <span id='message'> {this.state.message}</span><span style={{color:this.state.changeColor}}> {this.state.change} </span></div>
          <div>
            <span className="stats">High: $ {this.state.high} </span>
             <span className="stats">Low: $ {this.state.low} </span>
             <span className="stats">Open: $ {this.state.open} </span>
             <span className="stats">Close: $ {this.state.close} </span>    
          </div>
          <hr/>
        <Grid container className='classesdemo' justify="space-around" spacing={24} alignItems = "center">
         
            <Grid item xs={2}>
              <Title/>
            </Grid>

            <Grid item xs={2}>
              <Classic state={this.state}/>
            </Grid>

          <Grid item xs={2}>
            <Camarilla state={this.state}/>
          </Grid>

          <Grid item xs={2}>
            <Fib state={this.state}/>
          </Grid>

          <Grid item xs={2}>
            <Demark state={this.state}/>
          </Grid>
        </Grid>
        <hr/>
        <div className = 'footnote'>** Pivot Point data is only accurate before/after trading hours **</div>
      </div>
       </div>
       <div className = 'stock'>
       <h2>{this.state.symbol} holdings</h2>
       <TextField id="quantity" type="number"   label="quantity" onChange={this.enterQuantity}  />
       <span>  </span>
       <TextField id="price" type="number"  label="price" onChange={this.enterPrice}  />
      <br/>
       <Button size="small" style={{Height: '1vmin'}} id='button' variant="contained" color="primary" onClick={this.quantityEnter}> Add </Button>
         <br/>
         <br/>
         <div>Quantity @ price = total value</div>
       {this.state.holdings.map( (stock, index) => (<div> <span style={{color:'blue'}}>{stock.quantity}</span> x <span style={{color:'darkblue'}}>$ {stock.price} </span>= ${stock.quantity * stock.price} <span className='delete' onClick = {() => this.delete(index)  } >  delete </span></div>) )}
      <hr/>
      you have:  {this.state.shares} @ ${this.state.adjusted}

      <hr/>

      <div style={{margin:'2.5vh'}}></div>
      <div>
        <span className="stats" onClick = { ()=> this.sellingUpdate('current')} style={{color:'green'}}>Current</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('s3')} style={{color:'green'}}>S3</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('s2')} style={{color:'green'}}>S2</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('s1')} style={{color:'green'}}>S1</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('pp')} style={{color:'green'}}>Pivot</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('r1')} style={{color:'green'}}>R1</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('r2')} style={{color:'green'}}>R2</span>
        <span className="stats" onClick = { ()=> this.sellingUpdate('r3')} style={{color:'green'}}>R3</span>
      </div>
      <br/>
      {this.state.compareMessage}
      {this.state.selling.map( (stock, index) => (<div> <span style={{color:'blue'}}>{stock.quantity}</span> x <span style={{color:'darkblue'}}>$ {stock.price} </span>= ${(stock.quantity * stock.price).toFixed(2)} <span className='delete' onClick = {() => this.delete(index)  } >  delete </span></div>) )}
      {this.state.shares2 >0 &&
      <div>
      <hr/>
      you can sell:  {this.state.shares2} @ ${parseFloat(this.state.value2/this.state.shares2).toFixed(2)}
      <hr/>
      </div>}
      <div style={{margin:'5vh'}}></div>
      <div>
      <span className="stats" onClick = { ()=> this.store(1)} style={{color:'blue'}}>store </span>
      <span className="stats" onClick = { ()=> this.recall(1)} style={{color:'blue'}}>recall </span>
      Set1
            <span className="stats">Date: {this.state.date1.slice(30,45)} </span>
             <span className="stats">Close: $ {this.state.close1} </span>    
          </div>
          <div style={{margin:'2vh'}}>
      <span className="stats" onClick = { ()=> this.store(2)} style={{color:'blue'}}>store </span>
      <span className="stats" onClick = { ()=> this.recall(2)} style={{color:'blue'}}>recall </span>
      Set2
      <span className="stats">Date: {this.state.date2.slice(30,45)} </span>
             <span className="stats">Close: $ {this.state.close2} </span>    
          </div>
       </div>
    </div>
    );
  }
}

export default App;